body {
  font-family: 'Roboto', sans-serif;
}

html {
  scroll-behavior: smooth;
}

h1 {
  font-weight: 600;
  margin-top: 1em;
  margin-bottom: 1em;
}

.App {
  text-align: center;
}

.Main {
  padding-bottom: 100px;
}

.HenkilokuvaParagraph {
  padding-left: 2em;
  padding-right: 2em;
}

.footer {
  margin-top: 1rem;
  padding: 0.5rem;
  padding-top: 20px;
  background-color: #FDCA40;
  position: fixed;
  bottom: 0;
  left: 0;
  height: 80px;
  width: 100%;
}

.card {
  margin: 1rem;
  padding: 1.5rem;
}

@media only screen and (max-width: 768px) {
  .HenkilokuvaParagraph {
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
}
