.projektit {
    background: #FDCA40;
    background: linear-gradient(
      90deg,
      #FDCA40 0%,
      rgba(245, 222, 179, 1) 60%
    );
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding-top: 1rem;
    padding-bottom: 4rem;
  }

.esittely {
    text-align: left!important;
}

.esittelyParagraph {
    margin-top: 3rem;
    padding-right: 3rem;
}

.projektiImg {
    box-shadow: -2px 2px 5px rgba(0, 0, 0, 0.3);
}
  
@media only screen and (max-width: 768px) {
    .esittely {
        text-align: center!important;
    }
    .esittelyParagraph {
        margin-top: 3rem;
        padding-right: 0rem;
    }
  }
  